function set_pages() {
   // console.log('');
    var {
        pages
    } = __wxConfig;
    var arr, str, obj = {};
    pages_for: for (var i = pages.length - 1; i >= 0; i--) {
        arr = pages[i].split('/');
        if (arr[0] == '') {
            arr.shift();
        }
        page_for: for (var j = arr.length - 1; j >= 0; j--) {
            str = arr[j];
            if (str == 'pages' && j == 0) {
                str = arr[arr.length - 1];
                break page_for;
            }
            if (str != 'index') {
                if (arr[1] != str) {
                    str = arr[1] + '_' + str;
                }
                if (obj[str]) {
                    // str = arr[1] + '_' + str;
                    // if (obj[str]){
                    str += 2;
                    // }
                }
                // console.log(str);
                break page_for;
            }
        }
        obj[str] = pages[i];
    }
   // console.log(obj);
    return obj;
}
