//  var_path_split({path, start, is_data:true, _this}) path-> start->
function var_path_split({
    path,
    start,
    is_data = true,
    _this
}) {
    var reg = /[^\[\]\.]+/g;
    var arr = path.match(reg);

    var var_v = start || (is_data ? (_this || this).$data : _this); // var_value
    // console.log('', {
    //     path,
    //     start
    // });
    var new_path = arr[0];
    for (let v of arr) {
        if (var_v == undefined) {
            // console.log('', {
            //     new_path,
            //     var_v,
            //     v
            // })
            if (isNaN(v)) {
                var_v = {};
            } else {
                var_v = [];
            }
            if (is_data) {
                var that = _this || this;
               // console.log(this, new_path);
                that.setData({
                    [new_path]: var_v,
                })
            }
        }
        var_v = var_v[v || var_v.length];
        if (v != new_path) {
            // console.log(v, isNaN(v));
            if (isNaN(v)) {
                new_path += '.' + v;
            } else {
                new_path += `[${v}]`;
            }
        }
    }
   // console.log(': ', var_v);
    return var_v;
}

// 
function dataset(e) {
    // console.log('', e);
    if(e){
        var param = e.currentTarget.dataset.param;
        param && (param = JSON.parse(param));
        var obj = {
            ...e.detail,
            ...e.currentTarget.dataset,
            // ...e.currentTarget.dataset.param,
        };
        param && (obj = { ...obj, ...param });
        return obj;
    }else{
        return {};
    }
}

