// , ;  getUrl(url);
function getUrl(url){
 //   console.log(', ', url, url.indexOf('://') == -1, getApp().globalData.api);
    if (url.indexOf('://') == -1) {
        // console.log('this.$scope', this.$scope);
        // console.log('getApp()', getApp());
        url = getApp().globalData.api + url;
        // var config = require("../utils/config");
        // url = config.url + url;
    }
    if(
        window
        && window.location
        && window.location.protocol
        && ['http:', 'https:'].indexOf(window.location.protocol)!=-1
    ){
        // console.log(',   window.location.protocol', window.location.protocol);
        // console.log(',   getApp().globalData.api', getApp().globalData.api);
        url=url.replace(/^(http|https):/, window.location.protocol);
    }
    return url
}

// 
function delayJump(url = '/pages/index/index', duration = 1000, type = 1, delta=1) {
    // console.log('', {
    //     url,
    //     duration,
    //     type,
    //     delta,
    // });
    setTimeout(function() {
        var method = '';
        type -= 0;
        switch (type) {
            case 1:
                method = 'navigateTo';//
                break;
            case 2:
                method = 'redirectTo';//
                break;
            case 3:
                method = 'switchTab';// tabbar
                break;
            case 4:
                method = 'reLaunch';// 
                break;
            case 5:
              //  console.log(' /', url);
				return
                break;
            case 6:
                method = 'navigateBack';// 
                break;
            default:
                break;
        }
        uni[method]({
            url: url,
            delta: delta,
            fail(err) {
                // console.log('', err);
                toastTip('');
            }
        })
    }, duration)
};

//  
function buildRequest({
    url = '',
    param = {},
    method = 'POST',
    isDebug = false,
    isshowLoading = false
} = {}) {
    // console.log('', {
    //     url,
    //     param,
    //     method,
    //     isDebug,
    //     isshowLoading,
    // });
    if (isshowLoading) {
        uni.showLoading({
            title: '...'
        });
    }
    let timeStart = Date.now();
    return new Promise((resolve, reject) => {
        uni.request({
            url: getUrl(url),
            data: param,
            method: method,
            header: {
                'content-type': 'application/json', //  , "content-type": "application/x-www-form-urlencoded"
                'token': uni.getStorageSync('token'),
            },
            complete: (res) => {
                if (isshowLoading) {
                    uni.hideLoading()
                }
                if (isDebug) {
                    // console.log(`${Date.now() - timeStart}`);
                    // console.log(res.data)
                }
                if (res.statusCode >= 200 && res.statusCode < 300) {
                    resolve(res.data)
                } else {
                    reject(res)
                }
            }
        })
    })
}

//  
function request(url, param = {}, method = 'POST', isDebug, isshowLoading) {
    return buildRequest({
        url,
        param,
        isDebug: isDebug,
        isshowLoading: isshowLoading,
        method: method
    })
}

