//  delayJump
// : 1.data-url ; 2.data-type ; 3.data-time ; 4.data-check ; 
function jump(e) {
    var obj = dataset(e);
    var fixed_param_arr = ['url', 'time', 'type', 'check', 'phone_check', 'param']; //
    var {
        url,
        time = 0,
        type,
        check,
        phone_check,
    } = obj;
    if (!url) {
        // console.log('', url);
        return
    }
    // 
    if (check) {

        // login_check 
        if (login_check && login_check()) {
              delayJump(login_url, 0);
            return;
        }
    }
    if (uni.jump) {
        return;
    } else {
        uni.jump = true;
        uni.jump_time = time / 1000 + 0.5;
        setTimeout(() => {
            uni.jump = false;
        }, time + 500)
    }
    var param = '?'; //
    for (let i in obj) {
        if (fixed_param_arr.indexOf(i) != -1) {
            continue
        }
        if (param != '?') {
            param += '&'
        }
        param += `${i}=${obj[i]}`;
    }
    if (param == '?') {
        param = '';
    }

    // 
    if (phone_check) {
        // console.log(', ');
        var {
            user_info
        } = getApp().globalData;
        if (!user_info) {
            // toastTip('');
            getApp().globalData.phone_check_data = {
                url: type == 3 ? url : (url + param),
                type,
            }
            delayJump(`/${getApp().globalData.page_data.pages.common_accounts}`);
            return;
        }
    }
    // console.log(': ', url + param);
    delayJump(type == 3 ? url : (url + param), time, type);
}



//  bindinput='input_set_value' data-name='name' data-int='{{true}}'() catchtap='input_set_value'
function input_set_value(e) {
    var {
        name,
        int,
    } = dataset(e);
    var {
        value = '',
            current = '',
    } = e.detail;
    value || (value = current)
    if (int) {
        value = isNaN(parseInt(value)) ? 0 : parseInt(value);
    }
    console.log('', e, {
        name,
        value
    });
    try {
        this.setData({
            [name]: value
        });
    } catch (err) {
        // 
        var_path_split.call(this, {
            path: name,
            start: this.$data
        });
        this.setData({
            [name]: value
        });
    }
}

//  bindtap='set_value' data-name='name' data-value='{{index}}' data-method='push' catchtap='set_value'
function set_value(e) {
    var {
        name,
        value = '',
        index = 0,
        method,
        no_report,
    } = dataset(e);
    console.log('', e, {
        name,
        value,
        method,
    });
    var name_arr = name.split(/ *[, ] */g);
    var value_arr = [];
    if (typeof value == 'string') {
        value_arr = value.split(/ *[, ] */g);
    } else {
        value_arr.push(value);
    }
    console.log({
        name_arr,
        value_arr
    });
    name_arr.map((item, indexs, arr) => {
        if (!item) {
            console.log(' ', item);
            return;
        }
        var value = value_arr[indexs] == 'false' ? false :
            (
                value_arr[indexs] == undefined ?
                value_arr[indexs - 1] || value_arr[value_arr.length - 1] :
                value_arr[indexs]
            );
        // console.log({
        //     item,
        //     indexs,
        //     value
        // }, this);
        // 
        var data_v = var_path_split.call(this, {
            path: item,
            start: this.$data
        });
        if (no_report && data_v == value) {
            // console.log(', ', {
            //     name: item,
            //     value,
            //     data_v,
            // }, data_v == value);
            return
        }
        if (method == 'push' && index == indexs) {
            if (!Array.isArray(data_v)) {
                data_v = [];
            }
            data_v.push(value);
            value = data_v;
        }
       
        if (method == 'splice' && index == indexs) {
            if (!Array.isArray(data_v)) {
                data_v = [];
            }
            var arr_index = -1;
            while ((arr_index = data_v.indexOf(value)) != -1) {
                console.log('', arr_index);
                data_v.splice(arr_index, 1)
            }
            console.log('111', data_v.indexOf(value));
            value = data_v;
        }
        console.log(item, value);
        this.setData({
            [item]: value
        })
    })
}

//  bindtap='count_value' data-name='name' data-count='add/min' catchtap='count_value'
function count_value(e) {
    console.log('', e);
    var {
        name,
        count,
    } = dataset(e);
    var data_v = var_path_split.call(this, {
        path: name,
        start: this.$data
    });
    isNaN(data_v) && (data_v = 0);
    data_v = (data_v - 0) || 0;
    if (count == 'add') {
        data_v++;
    } else {
        data_v--;
    }
    this.setData({
        [name]: data_v
    })
}

